<template>
	<view >
		
		
		<view class="item" v-for="(item,index) in noticeList" :data-noticeId="item.noticeId" @click="toDetail">
			<view class="item_text">
				{{item.noticeTitle}}
			</view>
			<view class="item_time">
				{{new Date(item.noticeTime).toLocaleDateString()}}
			</view>
		</view>
		
		<button type="default" v-show="role==2" @click="toAdd">添加</button>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				noticeList:[],
				role:''
			}
		},
		methods: {
			toAdd(){
				let that = this;
				console.log("点击了发布")	
				uni.navigateTo({
					url:"../noticeAdd/noticeAdd"
				})
				
			},
			toDetail(e){
				var noticeId = e.currentTarget.dataset.noticeId
				uni.request({
					// url:'http://localhost:8091/power_distribution/addViewNum', 
							url: 'http://1.116.84.92:8080/addViewNum', 
					data: {//参数
						noticeId:noticeId
					},
					method:'POST',
					header: {
						'Access-Control-Allow-Credentials':true,
						'Access-Control-Allow-Origin':'http://localhost:8080/#/',
						'Content-type':'application/x-www-form-urlencoded'
					},
					success(res) {
						// console.log(res)
					}
				})
				uni.navigateTo({
					url:"../noticeDetail/noticeDetail?noticeId="+noticeId
				})
			}
		},
		onShow() {
			let that = this;
			uni.request({
				// url:'http://localhost:8091/power_distribution/findAllNotice', 
						url: 'http://1.116.84.92:8080/findAllNotice', 
				data: {//参数
				},
				method:'POST',
				header: {
					'Access-Control-Allow-Credentials':true,
					'Access-Control-Allow-Origin':'http://localhost:8080/#/',
					'Content-type':'application/x-www-form-urlencoded'
				},
				success(res) {
					console.log(res);
					that.noticeList = res.data;
				}
			})
			uni.getStorage({
				key:'role',
				success(res) {
					that.role = res.data
				}
			})
		}
		
		
	}
</script>

<style>
.item{
	width: 98%;
	height: 70rpx;
	line-height: 70rpx;
	border-bottom: 1px dotted #C8C7CC;
	margin: 0rpx auto;
	display: flex;
	justify-content: space-between;
	
}
.item:active{
	background-color: #F1F1F1;
}
.item_text{
	/* height: 50rpx; */
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	font-size: 35rpx;
}
.item_time{
	margin-left: 20rpx;
	font-size: 30rpx;
}
button{
	position: absolute;
	height: 50rpx;
	line-height: 50rpx;
	bottom: 5rpx;
	right: 5rpx;
}
</style>
